import React, { useContext, useEffect } from "react";
import Box from "@material-ui/core/Box";
import { HeroContext } from "../contexts/HeroContext";
// import "../assets/styles/sliderArrow.css";
import Empty from "./Empty";
import Characters from "./Characters";
import { ButtonsContext } from "../contexts/ButtonsContext";
import Title from "./Title";
import propTypes from "prop-types";
import Stats from "./Stats";
import ErrorBoundary from "../ErrorBoundary/index"

const MyTeam = () => {
  const { heroes, deleteHero } = useContext(HeroContext);
  const { setShow } = useContext(ButtonsContext);

  useEffect(() => {
    setShow(false);
  }, []);

  // const settings = {
  //   dots: true,
  //   infinite: false,
  //   slidesToShow: 4,
  //   slidesToScroll: 3,
  //   speed: 500,
  //   autoplaySpeed: 2000,
  //   autoplay: true,
  //   cssEase: "linear",
  //   responsive: [
  //     {
  //       breakpoint: 740,
  //       settings: {
  //         slidesToShow: 3,
  //         slidesToScroll: 2,
  //         initialSlide: 2,
  //       },
  //     },
  //     {
  //       breakpoint: 600,
  //       settings: {
  //         slidesToShow: 2,
  //         slidesToScroll: 2,
  //         initialSlide: 2,
  //       },
  //     },
  //     {
  //       breakpoint: 400,
  //       settings: {
  //         slidesToShow: 1,
  //         slidesToScroll: 1,
  //       },
  //     },
  //   ],
  // };

  return (
    <>
     {heroes.length >0 &&  <Title gutterBottom align="center" variant="h3">
        Mi equipo
      </Title>}
      <Box p={3}>
        <ErrorBoundary>
          <div>
        {/* <Slider {...settings} className="slider-position"> */}
          {heroes.length > 0 ? (
            heroes.map((characters) => {
              const { id } = characters;
              return (
                <Characters
                  key={id}
                  characters={characters}
                  deleteHero={deleteHero}
                />
              );
            })
          ) : (
            <Empty />
          )}
          </div>
        {/* </Slider> */}
        </ErrorBoundary>
        <Box>{heroes.length > 0 && <Stats />}</Box>
      </Box>
    </>
  );
};

MyTeam.propTypes = {
  heroes: propTypes.arrayOf(propTypes.object),
  deleteHero: propTypes.func,
};
export default MyTeam;
