import React from "react";
import { Table } from "@material-ui/core";
import { TableBody } from "@material-ui/core";
import TableCell from "@material-ui/core/TableCell";
import { TableContainer } from "@material-ui/core";
import { TableHead } from "@material-ui/core";
import { TableRow } from "@material-ui/core";
import { Paper } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";

function createData(
  name,
  speed,
  durability,
  intelligence,
  power,
  combat,
  strength
) {
  return { name, speed, durability, intelligence, power, combat, strength };
}

export const TableStats = ({ totalStatsHero }) => {
  const useStyles = makeStyles({
    head: {
      background: "rgb(54, 57, 63)",
    },
    tablecell: {
      color: "white",
    }
  });
  const classes = useStyles();
  const { speed, strength, durability, intelligence, power, combat } =
    totalStatsHero;
  const rows = [
    createData(
      "Total Stats",
      speed,
      durability,
      intelligence,
      power,
      combat,
      strength
    ),
  ];
  return (
    <TableContainer component={Paper}>
      <Table sx={{ minWidth: 700 }} aria-label="customized table">
        <TableHead>
          <TableRow className={classes.head}>
            <TableCell className={classes.tablecell}>
              Stats
            </TableCell>
            <TableCell className={classes.tablecell} align="center">
              Speed
            </TableCell>
            <TableCell className={classes.tablecell} align="center">
              Durability
            </TableCell>
            <TableCell className={classes.tablecell} align="center">
              Intelligence
            </TableCell>
            <TableCell className={classes.tablecell} align="center">
              Power
            </TableCell>
            <TableCell className={classes.tablecell} align="center">
              Combat
            </TableCell>
            <TableCell className={classes.tablecell} align="center">
              Strength
            </TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map((row) => (
            <TableRow key={row.name}>
              <TableCell component="th" scope="row">
                {row.name}
              </TableCell>
              <TableCell align="center">{row.speed}</TableCell>
              <TableCell align="center">{row.durability}</TableCell>
              <TableCell align="center">
                {row.intelligence}
              </TableCell>
              <TableCell align="center">{row.power}</TableCell>
              <TableCell align="center">{row.combat}</TableCell>
              <TableCell align="center">{row.strength}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
};

export default TableStats;
